<template>
  <div class="columns">
    <div class="column is-half-desktop">
      <div class="token-info">
        <div class="token-info__item">
          <div class="token-info__name">
            Total number of addresses
          </div>
          <div class="token-info__value">
            {{ list.length }}
          </div>
        </div>
        <div class="token-info__item">
          <div class="token-info__name">
            Total number of tokens to be sent
          </div>
          <div class="token-info__value">
            {{ toDecimals(totalTokens, null, 10) }} {{ getTokenSymbol }}
          </div>
        </div>
        <div class="token-info__item">
          <div class="token-info__name">
            Your token Balance
          </div>
          <div class="token-info__value">{{ toDecimals(tokenBalance) }} {{ getTokenSymbol }}</div>
        </div>
        <div class="token-info__item">
          <div class="token-info__name">
            Total number of transactions needed
          </div>
          <div id="totalNumberOfTx" class="token-info__value">
            {{ txs.length }}
          </div>
        </div>
        <div class="token-info__item">
          <div class="token-info__name">Your {{ networkConfig.currencyName }} Balance</div>
          <div class="token-info__value">
            {{ toDecimals(ethBalance) }} {{ networkConfig.currencyName }}
          </div>
        </div>
        <div class="token-info__item">
          <div class="token-info__name">
            Approximate cost of operation
          </div>
          <div class="token-info__value">
            {{ toDecimals(approximateTxCost) }} {{ networkConfig.currencyName }}
          </div>
        </div>
        <div class="token-info__item">
          <div class="token-info__name">
            Selected network speed (Gas Price)
          </div>
          <div class="token-info__value">{{ gasPriceSlider }} Gwei</div>
        </div>
        <div class="token-info__item">
          <div class="token-info__slider">
            <m-slider
              v-model="gasPriceSlider"
              :min="gasPrice.low"
              :max="gasPrice.fast + 1"
              :marks="gasSliderMarks"
              :step="gasPriceStep"
            ></m-slider>
          </div>
        </div>
      </div>
      <div v-if="manyTransactions" class="token-field">
        <div class="label">Multisender options for sending transactions</div>
        <div class="field">
          <b-radio v-model="sendStrategy" size="is-medium" :native-value="'selfGenerated'">
            Use self-generated key(recommended)
          </b-radio>
          <div v-if="sendStrategy === 'selfGenerated'">
            Your burner key: {{ accountSender.address }}
          </div>
        </div>
        <div class="field">
          <b-radio v-model="sendStrategy" size="is-medium" :native-value="'metamask'">
            Use Metamask(might be slow)
          </b-radio>
        </div>
        <p class="subtitle" v-html="infoText"></p>
      </div>
      <div v-if="!isGotError" class="token-field">
        <b-button
          id="confirm-proceed-button"
          type="is-info"
          :disabled="txsToRender.length > 0"
          rounded
          @click="authorizeButtonAction"
        >
          {{ actionButtonText }}
        </b-button>
      </div>
      <div v-if="topUpTx.txHash" class="token-field">
        <div class="label">Tx Hash of top-up process</div>
        <div class="txs">
          <div class="txs__item" :class="txStatusClass(topUpTx.status)">
            <div class="txs__status"></div>
            <a :href="txExplorerUrl('topUpTx')" class="txs__address" target="_blank">
              {{ txHashToRender('topUpTx') }}
            </a>
          </div>
        </div>
      </div>
      <div v-if="txsToRender.length > 0" class="token-field">
        <div class="label">Multisend transactions</div>
        <ol class="txs">
          <li
            v-for="{ txHash, status } in txsToRender"
            :key="txHash"
            class="txs__item"
            :class="txStatusClass(status)"
          >
            <div class="txs__status"></div>
            <a :href="txExplorerUrl('multisendTxs', txHash)" class="txs__address" target="_blank">
              {{ txHashToRender('multisendTxs', txHash) }}
            </a>
          </li>
        </ol>
      </div>
      <b-message type="is-danger" :active.sync="isGotError">
        There is a problem with some of your addresses.
      </b-message>
      <b-message :type="statusClass" :active.sync="allTxsMined">
        {{ statusText }}
        <div v-if="leftOversTx.txHash" class="token-field">
          <p class="subtitle">
            We are sending the remaining {{ networkConfig.currencyName }} to your address
            {{ ethAccount }}
          </p>
          <div class="label">Tx Hash of remaining {{ networkConfig.currencyName }}</div>
          <div class="txs">
            <div class="txs__item" :class="txStatusClass(leftOversTx.status)">
              <div class="txs__status"></div>
              <a :href="txExplorerUrl('leftOversTx')" class="txs__address" target="_blank">
                {{ txHashToRender('leftOversTx') }}
              </a>
            </div>
          </div>
        </div>
      </b-message>
    </div>
    <div class="column is-hidden-touch">
      <div class="wrap-image wrap-image--token">
        <img src="~assets/img/token.png" alt="" class="wrap-image__images" />
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-console */
import { mapState, mapGetters } from 'vuex'
import MSlider from '@/components/slider/slider'

export default {
  components: {
    'm-slider': MSlider
  },
  data() {
    return {
      sendStrategy: 'metamask',
      gasPriceStep: 0.1
    }
  },
  computed: {
    ...mapGetters('distribution', ['approximateTxCost']),
    ...mapGetters('token', ['toDecimals', 'getTokenSymbol']),
    ...mapGetters('metamask', ['networkConfig']),
    ...mapGetters('txHashKeeper', ['txExplorerUrl', 'txHashToRender', 'txStatusClass']),
    ...mapGetters('txStorage', [
      'txsToRender',
      'accountSender',
      'allTxsMined',
      'allTxsHaveSuccess'
    ]),
    ...mapState('distribution', ['list', 'totalTokens', 'txs']),
    ...mapState('token', ['tokenBalance']),
    ...mapState('metamask', ['ethBalance', 'gasPrice', 'ethAccount']),
    ...mapState('txHashKeeper', ['topUpTx', 'leftOversTx']),
    gasPriceSlider: {
      set(value) {
        this.$store.commit('metamask/CUSTOM_GAS_PRICE', value)
      },
      get() {
        return this.$store.state.metamask.gasPrice.custom
      }
    },
    isGotError() {
      return this.txs.length === 0
    },
    manyTransactions() {
      return this.txs.length >= 4
    },
    actionButtonText() {
      const signButtonText = this.accountSender.signature
        ? 'Proceed with self generated key'
        : 'Sign Key'
      const buttonName = this.sendStrategy === 'metamask' ? 'Proceed with Metamask' : signButtonText
      return buttonName
    },
    infoText() {
      const buttonName =
        this.sendStrategy === 'metamask'
          ? `We highly recommend using self-generated key option, since Metamask can be slow and does not always function properly.`
          : `This is the fastest recommended way to use multisender. <br /> <br />
          1. It will generate a burner key on your device <br />
          2. After the burner key receives the funds, it will automatically post all txs to the ${
            this.networkConfig.networkName
          } blockchain <br />
          3. Once the multisend is complete, the burner key will send the remaining ${
            this.networkConfig.currencyName
          } balance to your address`
      return buttonName
    },
    statusText() {
      return this.allTxsHaveSuccess
        ? 'Congratulations! You have succesfully finished your multisend.'
        : 'Some of your transactions failed. Please, contact support.'
    },
    statusClass() {
      return this.allTxsHaveSuccess ? 'is-success' : 'is-danger'
    },
    gasSliderMarks() {
      return {
        [this.gasPrice.low]: 'Slow',
        [this.gasPrice.fast]: 'Fast'
      }
    }
  },
  mounted() {
    this.$nuxt.$loading.finish()
    this.sendStrategy = this.manyTransactions ? 'selfGenerated' : 'metamask'
    this.gasPriceSlider = this.gasPrice.fast
  },
  beforeCreate() {
    if (this.$store.state.distribution.list.length === 0) {
      this.$store.dispatch('router/goToIndex')
    }
  },
  methods: {
    async authorizeButtonAction() {
      window.onbeforeunload = function() {
        return 'Prevent reload'
      }
      if (this.sendStrategy === 'metamask') {
        this.$store.dispatch('distribution/multisend')
        return
      }
      if (!this.accountSender.signature) {
        this.$store.dispatch('metamask/generateSignatureForSender')
      } else {
        const isSuccess = await this.$store.dispatch('metamask/topUpAuthorizedSignerBalance')
        if (isSuccess) {
          this.$store.dispatch('distribution/multisendWithSignature', {})
        }
      }
    }
  }
}
</script>
