<template>
  <div
    class="m-slider"
    role="slider"
    :aria-valuemin="min"
    :aria-valuemax="max"
    :aria-disabled="sliderDisabled"
  >
    <div
      ref="slider"
      class="m-slider__runway"
      :class="{ 'is-disabled': sliderDisabled }"
      @click="onSliderClick"
    >
      <div class="m-slider__bar" :style="barStyle"></div>
      <slider-button ref="button" v-model="sliderValue"></slider-button>
      <template v-if="markList.length > 0">
        <div>
          <div
            v-for="(item, key) in markList"
            :key="key"
            :style="getStopStyle(item.position)"
            class="m-slider__stop m-slider__marks-stop"
          ></div>
        </div>
        <div class="m-slider__marks">
          <slider-marker
            v-for="(item, key) in markList"
            :key="key"
            :mark="item.mark"
            :style="getStopStyle(item.position)"
          ></slider-marker>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import SliderButton from './button.vue'
import SliderMarker from './marker'

export default {
  name: 'MSlider',
  components: {
    SliderButton,
    SliderMarker
  },
  props: {
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    step: {
      type: Number,
      default: 1
    },
    value: {
      type: Number,
      default: 0
    },
    showTooltip: {
      type: Boolean,
      default: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    marks: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      sliderValue: null,
      oldValue: null,
      dragging: false,
      sliderSize: 1
    }
  },
  computed: {
    markList() {
      const marksKeys = Object.keys(this.marks)
      return marksKeys
        .map(parseFloat)
        .sort((a, b) => a - b)
        .filter(point => point <= this.max && point >= this.min)
        .map(point => ({
          point,
          position: ((point - this.min) * 100) / (this.max - this.min),
          mark: this.marks[point]
        }))
    },
    barSize() {
      return `${(100 * (this.sliderValue - this.min)) / (this.max - this.min)}%`
    },
    precision() {
      const precisions = [this.min, this.max, this.step].map(item => {
        const decimal = ('' + item).split('.')[1]
        return decimal ? decimal.length : 0
      })
      return Math.max.apply(null, precisions)
    },
    barStyle() {
      return {
        width: this.barSize,
        left: '0%'
      }
    },
    sliderDisabled() {
      return this.disabled
    }
  },
  watch: {
    value(val, oldVal) {
      if (
        this.dragging ||
        (Array.isArray(val) &&
          Array.isArray(oldVal) &&
          val.every((item, index) => item === oldVal[index]))
      ) {
        return
      }
      this.setValues()
    },
    dragging(val) {
      if (!val) {
        this.setValues()
      }
    },
    sliderValue(val) {
      this.$emit('input', val)
    },
    min() {
      this.setValues()
    },
    max() {
      this.setValues()
    }
  },
  mounted() {
    if (typeof this.value !== 'number' || isNaN(this.value)) {
      this.sliderValue = this.min
    } else {
      this.sliderValue = Math.min(this.max, Math.max(this.min, this.value))
    }
    this.oldValue = this.sliderValue
    this.$el.setAttribute('aria-valuetext', this.sliderValue)
    this.resetSize()
    window.addEventListener('resize', this.resetSize)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resetSize)
  },
  methods: {
    valueChanged() {
      return this.value !== this.oldValue
    },
    setValues() {
      if (this.min > this.max) {
        // eslint-disable-next-line no-console
        console.error('[Error][Slider]min should not be greater than max.')
        return
      }
      const val = this.value
      if (typeof val === 'number' && !isNaN(val)) {
        if (val < this.min) {
          this.$emit('input', this.min)
        } else if (val > this.max) {
          this.$emit('input', this.max)
        } else {
          this.sliderValue = val
          if (this.valueChanged()) {
            this.oldValue = val
          }
        }
      }
    },
    setPosition(percent) {
      this.$refs.button.setPosition(percent)
    },
    onSliderClick(event) {
      if (this.sliderDisabled || this.dragging) return
      this.resetSize()
      const sliderOffsetLeft = this.$refs.slider.getBoundingClientRect().left
      this.setPosition(((event.clientX - sliderOffsetLeft) / this.sliderSize) * 100)
      this.emitChange()
    },
    resetSize() {
      if (this.$refs.slider) {
        this.sliderSize = this.$refs.slider.clientWidth
      }
    },
    emitChange() {
      this.$nextTick(() => {
        this.$emit('change', this.value)
      })
    },
    getStopStyle(position) {
      return { left: position + '%' }
    }
  }
}
</script>
